import Vue from 'vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import routerConfig from './router'
import * as filters from './filter'
import app from './main'
import {host, lt} from './const'
import {toast} from './assets/js/toast'
import {DJUtils} from './assets/js/common'

//加载初始化样式
require('./assets/css/reset.css');
require('./assets/css/base.css');
require('./assets/iscroll/scrollbar.css');
require('./assets/css/swiper-3.4.0.min.css')

//加载js
require('./assets/js/DJDate.js');


// Router
Vue.use(VueRouter);

let router = new VueRouter();

routerConfig(router);

// Resource
Vue.use(VueResource);
Vue.http.options.root = host.host;
Vue.http.options.emulateJSON = true;
Vue.http.options.timeout = 6000;
Vue.http.interceptors.push((request, next) => {
    if (request.method == 'post') {
        toast.loading();
    }
    next((response) => {
        if (!response.request.params.minLink) {
            toast.loadEnd();
        }
        return response
    });
});

Vue.http.options.success = function (data, status) {
    //监听是否登录或者session过期
    //如果是跳转到登录界面
    if (data && !data.success && '405' == data.code) {
        lt.remove('user');
        var currPath = this.$route.path;
        var url = "#/login?from=" + encodeURIComponent(currPath);
        if (DJUtils.hasFrom(currPath)) {
            window.location.replace(url);
        } else {
            window.location.href = url;
        }
    }
};


//Transaction
Vue.transition('slide', {
    enter: function (el) {
        $(el).slideDown();
    },
    leave: function (el) {
        $(el).slideUp();
    }
});

// Filters
Vue.filter('hour-price', filters.hourPrice);
Vue.filter('area-filter', filters.areaFilter);
Vue.filter('img-host', filters.imgHost);
Vue.filter('n2c-filter', filters.numToChinese);
Vue.filter('publish-status', filters.publishStatus);
Vue.filter('task-status', filters.taskStatus);
Vue.filter('orderStatus', filters.orderStatus);
Vue.filter('zero-fill', filters.zeroFill);
Vue.filter('week-format', filters.weekFormat);
Vue.filter('week-class', filters.weekClass);
Vue.filter('ellipsis', filters.ellipsis);
Vue.filter('phoneMask', filters.phoneMask);
Vue.filter('dateFmtCHS', filters.dateFmtCHS);
Vue.filter('normMoney', filters.normMoney);
Vue.filter('dateFmtYM', filters.dateFmtYM);
router.start(app, '#app');



