import {area} from './assets/js/city.js'
import {host} from './const'

export let areaFilter = (code) => {
  if (code && code.length > 5) {
    return area.get(code);
  }
  return "未知";
};

export let hourPrice = (price) => {
  return price + "元/时";
};

export let imgHost = (path) => {
  return host.imgHost + path;
};

export let normMoney = (money) => {
  let mm = money * 1;
  let m = Math.round(mm).toString();
  if(m.length > 0 && m.length < 5){
    mm = parseFloat((mm + "").replace(/[^\d\.-]/g, "")).toFixed(2) + "";
    var l = mm.split(".")[0].split("").reverse(),
        r = mm.split(".")[1],
        t = "";
    for(var i = 0; i < l.length; i ++ ){
      t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? "," : "");
    }
    return t.split("").reverse().join("") + "." + r;
  }
  if(m.length > 4 && m.length < 7){
    return (mm/10000).toFixed(2) + "万";
  }
  if(m.length == 7){
    return (mm/1000000).toFixed(2) + "百万";
  }
  if(m.length == 8){
    return (mm/10000000).toFixed(2) + "千万";
  }
  if(m.length > 8 && m.length < 11){
    return (mm/100000000).toFixed(2) + "亿";
  }
  if(m.length == 11){
    return (mm/10000000000).toFixed(2) + "百亿";
  }
  if(m.length == 12){
    return (mm/100000000000).toFixed(2) + "千亿";
  }
  if(m.length > 12){
    return (mm/1000000000000).toFixed(2) + "万亿";
  }
};

export let numToChinese = (num)=> {
  let n = parseInt(num);
  switch (n) {
    case 1:
      return '一';
    case 2:
      return '二';
    case 3:
      return '三';
    default:
      return num;
  }
};


export let zeroFill = (val, length = 2)=> {
  var len = val.toString().length;
  while (len < length) {
    val = "0" + val;
    len++;
  }
  return val;
};


export let publishStatus = (status) => {
  switch (status) {
    case 0:
      return '待发布';
    case 1:
      return '发布中';
    case 2:
      return '已失效';
    case 3:
      return '已废弃';
  }
};

export let taskStatus = (status) => {
  switch (status) {
    case 0:
      return '执行中';
    case 1:
      return '未开始';
    case 2:
      return '已完结';
    case 3:
      return '已取消';
  }
};

export let orderStatus = (status,orderType) => {
  switch (status) {
    case 0:
      return '待发起';
    case 1:
      return orderType == 0 ?'确认中':'待确认';
    case 2:
      return '已拒绝';
    case 9:
      return '已删除';
    case 10:
      return '已达成';
  }
};

export let ellipsis = (str, len = 20) => {
  if (null == str) {
    return '';
  };
  if (str.length < len) {
    return str;
  }
  return str.substring(0, len) + "...";
};


//手机号码中间4位遮罩
export let phoneMask = (phone) => {
  phone = phone + '';
  let _phone = phone.substring(0,3) + '****' + phone.substring(7,11);
  return _phone;
};

/*datestr 格式：1990-1-1*/
export let dateFmtCHS = (datestr) => {
  var dateArr = datestr.split('-');
  var  newdate = datestr;
  if (dateArr.length == 3){
    newdate = dateArr[0] + '年' + zeroFill(dateArr[1]) + '月' + zeroFill(dateArr[2]) + '日';
  }
  return newdate;

};

/*ex:2016.10.17 -> 10.17*/
export let dateFmtYM = (dateStr) => {
  if (dateStr){
    return dateStr.substring(5,dateStr.length);
  }
};

export let weekFormat = (week) => {
  var formatStr = '';
  //console.log(week);
  week = week + "";
  if (week) {
    var weekArr = week.split(',');
    weekArr.sort();
    if (weekArr.length == 7) {
      return '不限';
    }
    var workday = 0, weekend = 0;
    for (var index in weekArr) {
      switch (parseInt(weekArr[index])) {
        case 1:
          formatStr += '一';
          workday++;
          break;
        case 2:
          formatStr += '二';
          workday++;
          break;
        case 3:
          formatStr += '三';
          workday++;
          break;
        case 4:
          formatStr += '四';
          workday++;
          break;
        case 5:
          formatStr += '五';
          workday++;
          break;
        case 6:
          formatStr += '六';
          weekend++
          break;
        case 7:
          formatStr += '日';
          weekend++
          break;
        default:
          break;
      }
    }
    if (weekArr.length == 5 && workday == 5) {
      return '工作日';
    }

    if (weekArr.length == 2 && weekend == 2) {
      return '周末';
    }
  }
  return formatStr;
};

//css
export let weekClass = (week) => {
  var formatStr = '';
  week = week + "";
  if (week) {
    var weekArr = week.split(',');
    weekArr.sort();
    if (weekArr.length == 7) {
      return '';
    }
    var workday = 0, weekend = 0;
    for (var index in weekArr) {
      switch (parseInt(weekArr[index])) {
        case 1:
          formatStr += '一';
          workday++;
          break;
        case 2:
          formatStr += '二';
          workday++;
          break;
        case 3:
          formatStr += '三';
          workday++;
          break;
        case 4:
          formatStr += '四';
          workday++;
          break;
        case 5:
          formatStr += '五';
          workday++;
          break;
        case 6:
          formatStr += '六';
          weekend++
          break;
        case 7:
          formatStr += '日';
          weekend++
          break;
        default:
          break;
      }
    }
    if (weekArr.length == 5 && workday == 5) {
      return 'weekdays';
    }

    if (weekArr.length == 2 && weekend == 2) {
      return 'weekends';
    }
  }
  return 'unnormal';
};





