export let st = {
  get(key){
    return window.sessionStorage.getItem(key);
  },
  set(key, val, t){
    try {
      window.sessionStorage.setItem(key, JSON.stringify(val));
    } catch (error) {
      if (t && t.length > 0) {
        toast.conform(t, null, false);
      }
    }
  },
  has(key){
    return window.sessionStorage.getItem(key);
  },
  getJson(key){
    return JSON.parse(window.sessionStorage.getItem(key));
  },
  remove(key){
    window.sessionStorage.removeItem(key);
  },
  keys: {
    menu: 'menu',//true= 点匠人，false= 匠人
    publishAdd: 'publish-add',
    orderEdit: 'order-edit',
    publishEdit: 'publish-edit',
    orderAdd: 'order-add',
    newOrderInfo: 'new-order-info',
    skillShow: 'skill-show',
    certificationEdit: 'certification-edit'
  }
};

