/**
 普通共用类
 */
export let DJUtils = {
    backHistory(){
        if (window.history.length == 1) {
            window.location.href = "#/home";
        } else {
            window.history.back();
        }
    },
    hasFrom(path){
        if (path && path.indexOf('from=') > 0) {
            return true;
        }
        return false;
    },
    phoneMask(phone){
        phone = phone + '';
        let _phone = phone.substring(0, 3) + '****' + phone.substring(7, 11);
        return _phone;
    },
    isLeap(val){
        return (val % 4 == 0 && val % 100 != 0) || (val % 400 == 0 && val % 100 == 0);
    },

    /*
     * 通过年月获取该月天数
     * */
    getCurrMonthDays(year, month){
        let min = [4, 6, 9, 11];
        if (min.indexOf(month) > -1) {
            return 30;
        } else if (month == 2) {
            if (this.isLeap(year)) {
                return 29;
            } else {
                return 28;
            }
        } else {
            return 31;
        }
    },

    //date
    stringToDate(dateStr) {
        if (dateStr) {
            if (dateStr.includes('.')) {
                dateStr = dateStr.replace(/\./g, "/");
            } else {
                dateStr = dateStr.replace(/-/g, "/");
            }
            return new Date(dateStr);
        }
    },

    calValidDays(startDate, endDate, validWeek, workTime){
        console.log('st:' + startDate + ',et:' + endDate + ',validWeek:' + validWeek + ',workTime:' + workTime);
        var hours = 0;
        if (startDate && endDate && validWeek && workTime) {
            var days = 0;
            var tempDate = this.stringToDate(startDate);
            var tempEndDate = this.stringToDate(endDate);
            while (tempDate.getTime() <= tempEndDate.getTime()) {
                var tempWeek = tempDate.getDay() == 0 ? 7 : tempDate.getDay();
                if (validWeek.includes(tempWeek)) {
                    days++;
                }
                tempDate.setDate(tempDate.getDate() + 1);
            }

            if (days > 0) {
                console.log('days:' + days);
                var timePeriodArr = workTime.split(' ');
                console.log('timePeriodArr:' + timePeriodArr);
                for (var i = 0; i < timePeriodArr.length; i++) {
                    var tp = timePeriodArr[i];
                    var timeArr = tp.split('-');
                    var stTimeArr = timeArr[0].split(':');
                    var stMins = parseInt(stTimeArr[0]) * 60 + parseInt(stTimeArr[1]);
                    var etTimeArr = timeArr[1].split(':');
                    var etMins = parseInt(etTimeArr[0]) * 60 + parseInt(etTimeArr[1]);

                    hours += (etMins - stMins) / 60;
                }
            }
        }
        return hours * days;
    },

    isNotBlank(obj){
        if (obj && obj != '' && obj != null && typeof (obj) != 'undefined') {
            return true;
        }
    },
    checkDateFmt(date){//yyyy-mm-dd
        let result = date.match(/((^((1[8-9]\d{2})|([2-9]\d{3}))(-)(10|12|0?[13578])(-)(3[01]|[12][0-9]|0?[1-9])$)|(^((1[8-9]\d{2})|([2-9]\d{3}))(-)(11|0?[469])(-)(30|[12][0-9]|0?[1-9])$)|(^((1[8-9]\d{2})|([2-9]\d{3}))(-)(0?2)(-)(2[0-8]|1[0-9]|0?[1-9])$)|(^([2468][048]00)(-)(0?2)(-)(29)$)|(^([3579][26]00)(-)(0?2)(-)(29)$)|(^([1][89][0][48])(-)(0?2)(-)(29)$)|(^([2-9][0-9][0][48])(-)(0?2)(-)(29)$)|(^([1][89][2468][048])(-)(0?2)(-)(29)$)|(^([2-9][0-9][2468][048])(-)(0?2)(-)(29)$)|(^([1][89][13579][26])(-)(0?2)(-)(29)$)|(^([2-9][0-9][13579][26])(-)(0?2)(-)(29)$))/);
        return date == '永久' || !!result;
    },
    isStandalone(){
        let u = window.navigator.userAgent;
        let Agents = new Array("iPhone", "iPad", "iPod");
        let flag = false;
        for (let v = 0; v < Agents.length; v++) {
            if (u.indexOf(Agents[v]) > 0) {
                flag = true;
                break;
            }
        }
        return flag && window.navigator.standalone == true;
    }
};


