import $ from 'jquery';

let alertT = 0;
let loadT = 0;

let doConform = function () {
  $("#t-conform .cancel").unbind('click');
  $("#t-conform .do").unbind('click');
  $("#t-conform").addClass('tran-scale1');
  setTimeout(function () {
    $("#t-conform").fadeOut(100);
    $("#t-mask").fadeOut(100);
  }, 100);
};

let readMsg = function () {
  $("#t-message .close-white").unbind('click');
  $("#t-mask").unbind('click');
  $("#t-message").addClass('tran-scale1');
  $("#t-message .opt").addClass('hide');
  setTimeout(function () {
    $("#t-message").fadeOut(100);
    $("#t-mask").fadeOut(100);
  }, 100);
};

let closeCommit = function () {
  $("#t-commit .close").unbind('click');
  $("#t-commit .do").unbind('click');
  $("#t-commit .cancel").unbind('click');
  $("#t-commit").addClass('tran-scale1');
  setTimeout(function () {
    $("#t-commit").fadeOut(100);
    $("#t-mask").fadeOut(100);
  }, 100);
}

export let toast = {
  alert(msg, s = 1500){
    clearTimeout(alertT);
    $("#t-alert").css("left", "0");
    $("#t-alert").css("margin", "auto");
    $("#t-alert").html(msg);
    if ($(window).width() > $("#t-alert").width()) {
      $("#t-alert").css("marginLeft", "-" + ($("#t-alert").width() / 2) + "px");
      $("#t-alert").css("left", "50%");
    } else {
      $("#t-alert").css("margin", "0 .1rem");
    }
    $("#t-alert").fadeIn(500);
    alertT = setTimeout(function () {
      $("#t-alert").fadeOut(500);
    }, s);
  },
  mask(f, full = false){
    clearTimeout(loadT);
    if (full) {
      $("#t-mask").removeClass('z-index-2').addClass('z-index-1');
    } else {
      $("#t-mask").removeClass('z-index-1').addClass('z-index-2');
    }
    $("#t-mask").fadeIn(100);
    if (f) {
      $("#t-mask").bind('click', f);
    }
  },
  hide_mask(){
    if ($("#t-mask").attr("display") != 'none') {
      $("#t-mask").fadeOut(100);
      $("#t-mask").unbind('click');
    }
  },
  conform(msg, f, isOption = true, f2, tip = {do: '确定', cancel: '取消'}){
    $("#t-conform .msg").html(msg);
    if (!isOption) {
      $("#t-conform .cancel").removeClass('show').addClass('hide');
    } else {
      $("#t-conform .cancel").removeClass('hide').addClass('show');
      $("#t-conform .cancel").bind('click', function () {
        if (f2) {
          f2();
        }
        doConform();
      });
    }
    $("#t-conform .do").bind('click', function () {
      doConform();
      if (f) {
        f();
      }
    });
    $("#t-conform .do").html(tip.do);
    $("#t-conform .cancel").html(tip.cancel);
    $("#t-mask").removeClass('z-index-2').addClass('z-index-1');
    $("#t-conform").addClass('tran-scale1');
    $("#t-conform").fadeIn(100);
    $("#t-mask").fadeIn(100);
    $("#t-conform").removeClass('tran-scale1');
  },
  message(msg, title = '消息',operator,date){
    $("#t-message .title").html(title);
    $("#t-message .msg").html(msg);

    if (operator && date){
      $("#t-message .operator").html(operator);
      $("#t-message .date").html(date);
      $("#t-message .opt").removeClass('hide');
    }

    $("#t-message .close-white").bind('click', readMsg);
    $("#t-mask").bind('click', readMsg);
    $("#t-mask").removeClass('z-index-2').addClass('z-index-1');
    $("#t-message").addClass('tran-scale1');
    $("#t-message").fadeIn(100);
    $("#t-mask").fadeIn(100);
    $("#t-message").removeClass('tran-scale1');
  },
  commit(fn, title = '请说明原因', tip = '请填写原因', content = ''){
    $("#t-commit .title").html(title);
    $("#t-commit .edit").val(content);
    $("#t-commit .edit").attr('placeholder', tip);
    $("#t-commit .close").bind('click', closeCommit);
    $("#t-commit .do").bind('click', function () {
      fn($("#t-commit .edit").val());
    });
    $("#t-commit .cancel").bind('click', closeCommit);

    $("#t-mask").removeClass('z-index-2').addClass('z-index-1');
    $("#t-commit").addClass('tran-scale1');
    $("#t-commit").fadeIn(100);
    $("#t-mask").fadeIn(100);
    $("#t-commit").removeClass('tran-scale1');
  },
  doCommit(){
    closeCommit();
  },
  loading(){
    clearTimeout(loadT);
    $("#t-mask").removeClass('z-index-2').addClass('z-index-1');
    $("#t-mask").fadeIn(100);
    $("#t-loading").fadeIn(100);
  },
  loadEnd(){
    loadT = setTimeout(function () {
      $("#t-mask").fadeOut(100);
    }, 100);
    $("#t-loading").fadeOut(100);
  },
  destroy(){
    if ($("#t-mask").attr("display") != 'none') {
      $("#t-mask").fadeOut(100);
      $("#t-mask").unbind('click');
    }
    if ($("#t-conform").attr("display") != 'none') {
      $("#t-conform .cancel").unbind('click');
      $("#t-conform .do").unbind('click');
      $("#t-conform").fadeOut(100);
    }
    if ($("#t-message").attr("display") != 'none') {
      $("#t-message").fadeOut(100);
    }
    if ($("#t-commit").attr("display") != 'none') {
      $("#t-commit .cancel").unbind('click');
      $("#t-commit .do").unbind('click');
      $("#t-commit").fadeOut(100);
    }
  },
  type: {
    success: 'success',
    fail: 'fail'
  }
};






